@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body, html {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

* {
  box-sizing: border-box;
}
.App {
  display: flex;
  height: 100%;
  max-width: 1100px;
  margin: 0 auto;
  position: relative;
}

.App__menu {
  width: 300px;
  height: 100%;
  position: fixed;
  background-color: transparent;
}

.App__title {
  padding: 5px 10px;
  font-weight: 300;
}

.App__tasks {
  margin-left: 300px;
  width: 100%;
  height: 100vh;
}

.Tasks {
  padding: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.Tasks__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding-right: 16px;
}

.Tasks__title {
  font-size: 20px;
  margin-left: 32px;
}

.Tasks__tasks {
  width: 100%;
  max-width: 1000px;
}

.Tasks__createTask {
  border-bottom: 1px solid rgba(0,0,0,0.12) !important;
}

.Tasks__createTaskIcon {
  padding-left: 9px !important;
}
.Task {
  border-bottom: 1px solid rgba(0,0,0,0.12);
}
.Home {
  padding: 10px 50px;
}
